.about_container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 10%;
}

.about_me_img {
  width: 100%;
  height: 100%;
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
}

.about_me_img img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* ensures it fills the container */
  border-radius: 2rem;
}

.about_me_img:hover {
    transform: rotate(0deg);
}

.about_cards {

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about_card {
    background: var(--color-gb-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
    transition: var(--transition);
}

.about_card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.about_icon {
    color: var(--color-primary);
    font-size: 1.4rem;
}

.about_card h5 {
    font-size: 0.8rem;

}

.about_card small {
    font-size: 0.6rem;
    color: var(--color-light);
}

.about_content p {
    color: var(--color-light);
    padding-top: 1rem;
    font-size: 0.8rem;
}

@media screen and (max-width:1024px) {
    .about_container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about_me {
        width: 40%;
        margin: 0rem auto 2rem;
    }

    .about_content p {
        margin: 1rem auto 1.5rem;
    }
}

/* media tablet */
@media screen and (max-width:600px) {

    .about_me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about_cards {
        grid-template-columns: 1fr;
        gap: 1rem;
        margin-bottom: 2rem;
    }

    .about_content {
        text-align: center;

    }
}